const coins = ['Bitcoin', 'Ethereum', 'XRP', 'Litecoin', 'Cardano', 'Bitcoin Cash', 'Polkadot', 'Monero', 'Neo']

const formatData = (cur) => {
  let currency = ''
  let i = 0
  Object.keys(cur).forEach(name => {
    const price = Math.floor(cur[name]['USD']['PRICE'] * 100) / 100
    const difference = Math.floor(cur[name]['USD']['CHANGE24HOUR'] * 100) / 100
    const chang24hour = () => Math.floor(((((price + difference) / price) - 1) * 10000)) / 100
    const imgSrc = `/img/${name}.png`
    currency += `
        <tr>
        <td>${Number(i)+1}</td>
        <td>
          <div class="coin">
            <img src="${imgSrc}" alt="${name}">
            <p>${coins[i]} <span>${name}</span></p>
          </div>
        </td>
        <td>$${price}</td>
        <td><p class="${difference > 0 ? 'uk-text-success' : 'uk-text-danger'}">${chang24hour()}%</p></td>
      </tr>
      `
    i++
  })

  const tableBody = document.querySelector('#table-body')
  tableBody.innerHTML = currency
}

fetch('https://min-api.cryptocompare.com/data/pricemultifull?fsyms=BTC,ETH,XRP,LTC,ADA,BCH,DOT,XMR,NEO&tsyms=USD&api_key={fa531fa2e07d2c7335fb8906264ccd1f2182f016c8f80f704b05540984ab8729}')
  .then(res => res.json())
  .then(data => {
    loader.style.display = 'none'
    formatData(data.RAW)}
  )

